import React, { useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import './NewsSection.scss';
import CardNews from '../../../../components/ui/CardNews/CardNews';
import upcomingEvents from '../../../News/informationNews';

const NewsSection = () => {
  const latestNews = upcomingEvents.slice(0, 4);
  const sectionRef = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add('news-section--visible');
          }
        });
      },
      { threshold: 0.1 }
    );

    const currentSectionRef = sectionRef.current;

    if (currentSectionRef) {
      observer.observe(currentSectionRef);
    }

    return () => {
      if (currentSectionRef) {
        observer.unobserve(currentSectionRef);
      }
    };
  }, []);

  return (
    <section className="news-section" ref={sectionRef}>
      <div className="news-section__container">
        <div className="news-section__header">
          <div className="news-section__title-wrapper">
            <div className="news-section__title-line"></div>
            <h2 className="news-section__title">ПОСЛЕДНИЕ НОВОСТИ</h2>
          </div>
          <Link to="/news" className="news-section__view-all">
            Все Новости
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M14 5L21 12M21 12L14 19M21 12H3" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
            </svg>
          </Link>
        </div>

        <div className="news-section__grid">
          {latestNews.map((news, index) => (
            <div className="news-section__card-wrapper" key={index} style={{ animationDelay: `${index * 0.1}s` }}>
              <CardNews
                image={news.image}
                title={news.title}
                description={news.description}
                date={`Март ${news.date}, 2025`}
                url={news.url}
                className="news-section__card"
              />
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default NewsSection;
